<template>
    <div class="ibox">
        <div class="ibox-title">
            查询条件
            <span class="icon is-angle" @click="showQuery = !showQuery">
                <i class="fa" :class="{'fa-angle-down': !showQuery,'fa-angle-up': showQuery }"></i>
            </span>
        </div>
        <div v-show="showQuery">
            <div class="ibox-filter">
                <el-form :inline="true" label-position="top">
                    <el-form-item label="状态">
                        <el-select class="auto-full col-item"
                                   v-model="filter.cmt_status"
                                   size="small"
                                   @change="search"
                                   filterable
                                   clearable>
                            <el-option
                                    v-for="(item, index) in query.status"
                                    :key="item.key"
                                    :label="item.name"
                                    :value="item.key">
                            </el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="审核人员">
                        <el-autocomplete
                                class="inline-input auto-full col-item"
                                size="small"
                                v-model="memberName"
                                :fetch-suggestions="querySearch"
                                placeholder="全部审核人员"
                                @select="search"
                                :disabled="disabledMember"
                        ></el-autocomplete>
                    </el-form-item>
                    <el-form-item label="发评用户ID">
                        <el-input
                                class="col-item"
                                size="small"
                                @keyup.enter.native="search"
                                placeholder="发评用户ID"
                                v-model="filter.member_id">
                        </el-input>
                    </el-form-item>
                    <el-form-item label="发评用户昵称">
                        <el-input
                                class="col-item"
                                size="small"
                                @keyup.enter.native="search"
                                placeholder="发评用户昵称"
                                v-model="filter.nickname">
                        </el-input>
                    </el-form-item>
                    <el-form-item label="发评用户手机">
                        <el-input
                                class="col-item"
                                size="small"
                                @keyup.enter.native="search"
                                placeholder="发评用户手机"
                                v-model="filter.telephone">
                        </el-input>
                    </el-form-item>
                    <el-form-item label="发评用户IP">
                        <el-input
                                class="col-item"
                                size="small"
                                @keyup.enter.native="search"
                                placeholder="发评用户IP"
                                v-model="filter.ip">
                        </el-input>
                    </el-form-item>
                <!--</el-form>-->
            <!--</div>-->
            <!--<div class="ibox-filter">-->
                <!--<el-form :inline="true" label-position="top">-->
                    <el-form-item label="来源">
                        <el-select v-model="filter.source"
                                   size="small"
                                   @change="search"
                                   filterable clearable
                                   class="auto-full col-item" >
                            <el-option
                                    v-for="(item, index) in query.source"
                                    :key="item.key"
                                    :label="item.name"
                                    :value="item.key">
                            </el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="领域">
                        <el-select v-model="filter.type"
                                   size="small"
                                   @change="search"
                                   filterable clearable
                                   class="auto-full col-item" >
                            <el-option label="全部领域" value=""></el-option>
                            <el-option v-for="(item, index) in categoryList"
                                       :key="item.id"
                                       :label="item.name"
                                       :value="item.id">
                            </el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="区域">
                        <el-select v-model="queryRegion"
                                   size="small"
                                   @change="search"
                                   filterable clearable
                                   class="auto-full col-item" >
                            <el-option
                                    v-for="(item, index) in query.region"
                                    :key="item.key"
                                    :label="item.name"
                                    :value="item.key">
                            </el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="内容ID">
                        <el-input
                                class="col-item"
                                size="small"
                                @keyup.enter.native="search"
                                placeholder="内容ID"
                                v-model="filter.content_id">
                        </el-input>
                    </el-form-item>
                <!--</el-form>-->
            <!--</div>-->
            <!--<div class="ibox-filter">-->
                <!--<el-form :inline="true" label-position="top">-->
                    <el-form-item label="分组">
                        <el-select v-model="filter.sensitive_group"
                                   size="small"
                                   @change="search"
                                   filterable
                                   clearable
                                   class="auto-full col-item">
                            <el-option label="全部分组" value=""></el-option>
                            <el-option
                                    v-for="(item, index) in groupList"
                                    :key="item.id"
                                    :label="item.formatName"
                                    :value="item.id">
                            </el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="评论关键字">
                        <el-input
                                class="col-item"
                                size="small"
                                @keyup.enter.native="search"
                                placeholder="评论关键字"
                                v-model="filter.comment">
                        </el-input>
                    </el-form-item>
                    <el-form-item label="显示条数">
                        <el-select v-model="filter.num"
                                   size="small"
                                   @change="search"
                                   filterable
                                   clearable
                                   class="auto-full col-item">
                            <el-option
                                    v-for="(item, index) in query.selectNum"
                                    :key="item.key"
                                    :label="item.name"
                                    :value="item.key">
                            </el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="排序">
                        <el-select v-model="filter.order"
                                   size="small"
                                   @change="search"
                                   filterable
                                   clearable
                                   class="auto-full col-item" >
                            <el-option
                                    v-for="(item, index) in query.sortType"
                                    :key="item.key"
                                    :label="item.name"
                                    :value="item.key">
                            </el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="属性">
                        <el-checkbox v-model="filter.is_push_content" @change="search" true-label="1" false-label="0">
                            推送内容
                        </el-checkbox>
                        <el-checkbox v-model="filter.is_hot_content" @change="search" true-label="2" false-label="0">
                            热推内容
                        </el-checkbox>
                        <el-checkbox v-model="filter.is_top_content" @change="search" true-label="2" false-label="0">
                            置顶内容
                        </el-checkbox>
                    </el-form-item>
                </el-form>
            </div>
            <div class="ibox-filter">
                <el-form :inline="true" label-position="top">
                    <el-form-item label="选择时间范围">
                        <el-date-picker
                                v-model="dateTimeRange"
                                @change="search"
                                size="small"
                                class="pos-right col-item"
                                type="datetimerange"
                                placeholder="选择时间范围">
                        </el-date-picker>
                    </el-form-item>
                    <el-form-item label="操作">
                        <el-button type="primary"
                                   class="auto-full col-item"
                                   @click="search">查询</el-button>
                    </el-form-item>
                </el-form>
            </div>
        </div>
    </div>
</template>

<style scrop >

    .auto-full {
        width: 100%;
    }

    .col-item {
        margin-bottom: 10px;
    }

    .pos-right {
        float: right;
    }

</style>

<script>
import dateFormat from '../../module/DateFormat';
import QuRequest from '../../core/QuRequest';
import sensitiveConfig from '../../module/SensitiveConfig';

import {
	COMMENT,
	commentStatus,
	source,
	region,
	selectNum,
	sortType
} from '../../module/CommentConfig';


const quRequest = new QuRequest();

export default {
	created() {
		this.filter = this.$queryFilter();
		this.$updateRouter(this.filter);
	},
    data(){
        return {
			recordList: [],
			categoryList: {},
			groupList: {},

            showQuery: true,

            query: {
				type: COMMENT,
                status: commentStatus,
                source: source,
				region: region,
				selectNum: selectNum,
				sortType: sortType
            },

            filter: {
				comment: '', // 评论关键词
				member_id: '', // member_id
				nickname: '', // nickname
				telephone: '', //telephone
                ip: '', // ip
                type: '', // 文章分类
				content_id: '',// content_id
				source: '0', // 文章来源. 0:全部 1:编辑发布 2:合作媒体 3:自媒体
				sensitive_group: '', // 敏感词分组
				op_id: '', //编辑id
				cmt_status: '0', // 评论状态
				cmt_create_time_start: dateFormat() + ' 00:00:00', // 评论时间
				cmt_create_time_end: dateFormat() + ' 23:59:59', // 评论时间
				order: '+', // 时间顺序。-:倒序 +:正序
                num: 20,
                is_push_content: '0', // 是否推送文章.1是0全部。
                is_hot_content: '0', // 是否热推文章. 2是0全部。
                is_top_content: '0', // 是否置顶文章. 2是0全部。,
                page: '1'
            },
			queryRegion: '1'
        }
    },
	computed: {
		memberName:{
			get(){
				if(this.filter.op_id){
					for(let i = 0, len = this.recordList.length; i < len; i++){
						if(this.recordList[i].member_id == this.filter.op_id){
							return this.recordList[i].member_name;
						}
					}
				} else {
					return ''
				}
			},
			set(newValue){
				if(newValue){
					for(let i = 0, len = this.recordList.length; i < len; i++){
						if(this.recordList[i].member_name == newValue){
							this.filter.op_id = this.recordList[i].member_id;
						}
					}
				} else {
					this.filter.op_id = ''
				}
			}
		},
		dateTimeRange: {
			set(newValue){
				this.filter.cmt_create_time_start = this.formatDate(newValue[0]);
				this.filter.cmt_create_time_end = this.formatDate(newValue[1]);
			},
			get(){
				return [this.filter.cmt_create_time_start,
                    this.filter.cmt_create_time_end]
			}
		},
        disabledMember(){
			let type = this.query.type;
            this.filter.op_id = '';
			if(this.filter.cmt_status === type.PASS
				|| this.filter.cmt_status === type.DENY
                || this.filter.cmt_status === type.SELF ){
				return false;
            } else {
				return true;
            }
        }
	},
    methods: {
		handleSelect(item){
		},
		formatDate(sDate){
			let date = new Date(sDate);
			let y = date.getFullYear();
			let m = this.addZero(date.getMonth() + 1);
			let d = this.addZero(date.getDate());
			let h = this.addZero(date.getHours());
			let min = this.addZero(date.getMinutes());
			let s = this.addZero(date.getSeconds());
		    return [y, m, d].join('/') + ' ' + [h, min, s].join(':');
        },
		addZero: function(num) {
			return (num > 9 ? '' : '0') + num
		},
		querySearch(queryString, cb){
			let array = [];
			this.recordList.forEach(function(item){
				if(item.member_name.indexOf(queryString) > -1){
					array.push({
						key: item.member_id,
						value: item.member_name
					})
				}
			})
			cb(array);
		},
        search(){
			let query = Object.assign({}, this.filter);
			query.page = 1;
			this.$emit('query', query);
        }
    },
    mounted() {
		quRequest.send(quRequest.Apis.qupost.adminMember.editorList) // 获取审核人员
            .then(res => {
				this.recordList = res.data;
            });

		quRequest.send(quRequest.Apis.qukan.content.getDefaultChannelList)// 获取领域分类
			.then(res => {
				this.categoryList = res.data;
			});

		quRequest.send(quRequest.Apis.qukan.sensitiveword.getGroupList)// 获取敏感词分组
            .then(res => {
				this.groupList = sensitiveConfig.formatGroup(res.data);
            });

		this.search();
    }
}
</script>



