import QuRequest from '../core/QuRequest';
import q from 'q';

const quRequest = new QuRequest();

function unDeckContent(content) {
	return (content || '').replace(/(data-)src=(?:"|').*?(?:"|')|(data-size=".*?")/gi, function(match, $1, $2) {
		return match.replace($1, "").replace($2, "");
	});
}

/**
 * @param {string} wrap 必须还有 $word$ 关键字,用于标示替换词
 * @param {arrary} words 敏感词
 * @param {string} content 文本内容
 * @return {string} 替换后的文本
 */

function highSub(item) {
	let word = item.word;
	let list = item.subwordpos;
	let level = item.level;
	if(list){
		if(list.length > 1){
			let firstPos = list[0];
			let secondPos = list[1];
			let firstWord = word.substring(firstPos[0], firstPos[1]);
			let normalWord = word.substring(firstPos[1], secondPos[0]);
			let secondWord = word.substring(secondPos[0]);
			return `<span class="sensitive-high-${level} sensitive-multiple">${firstWord}</span>
					${normalWord}
					<span class="sensitive-high-${level} sensitive-multiple">${secondWord}</span>`
		} else if(list.length > 0){
			return `<span class="sensitive-high-${level}">${word}</span>`
		}
	}
	return word;
}

function pureContent(content) {
	// let patt = /(?:<\w+(?:"[^"]*"|[^>])*>|<\/\w+>|[^<>]+)/i;
	let patt = /(?:<\w+(?:"[^"]*"|[^>])*>|<\/\w+>|[^<>]+)/g;
	let tag = /<(?:.|\s)*?>/;
	let result;
	let index = 0;
	let last = 0;
	let array = [];
	while ((result = patt.exec(content)) !== null) {
		if (!tag.test(result)) {// 是标签
			array.push([result.index, patt.lastIndex]);
		}
	}
	return array;
}

function contentWords(contents, words) {
	let contLength = contents.length;
	let wordLength = words.length;
	let contIndex = 0;
	let wordIndex = 0;
	let resultList = [];
	while (contIndex < contLength && wordIndex < wordLength){
		let contItem = contents[contIndex];
		let wordItem = words[wordIndex];
		if(wordItem.start >= contItem[0]
			&& wordItem.end < contItem[1]){
			resultList.push(wordItem);
			wordIndex++;
		} else {
			if(wordItem.start > contItem[1]){
				contIndex++;
			} else {
				wordIndex++;
			}
		}
	}
	return resultList;
}


function highWord(content, words){
	let tagArray = pureContent(content);
	words = contentWords(tagArray, words);
	if(words && words.length > 0){
		let contentArray = [];
		let index = 0;
		words.forEach( item => {
			contentArray.push(content.substring(index, item.start));
			contentArray.push(highSub(item));
			index = item.start + item.word.length;
		});
		contentArray.push(content.substring(index));
		return contentArray.join('')
	} else {
		return content
	}
}

function sensitiveHigh(obj, param, type){
	let list = [];
	let requestArray = [];
	if(Object.prototype.toString.call(param) === '[object Array]'){
		list = param;
	} else {
		list = [param];
	}
	list.forEach( item => {
		if(item &&  obj[item]){
			let content = obj[item];
			requestArray.push(
				quRequest.send(
					quRequest.Apis.qupost.contentAudit.checkSensitiveWord,
					{
						type: type,
						content: content
					}
				).then( res => {
					obj[item] = highWord(content, res.data.words);
					return res.data && res.data.action;
				})
			)
		}
	});

	return q.all(requestArray).then( res => {
		res = res || [];
		let status = '';
		for(let i = 0, len = res.length; i < len; i++){
			let item = res[i];
			if(item.indexOf('confirm') > -1){
				status = 'confirm';
				break;
			} else if(item.indexOf('highlight') > -1){
				status = 'highlight'
			} else {
				status = status || '';
			}
		}
		return status;
	}, res => {
		throw new Error(res.message || '未知错误')
	}).catch(reason => {
		throw reason;
	})
}

export default {unDeckContent, sensitiveHigh, highWord, highSub};



