<template>
    <div class="comment-box"
         v-loading="loading"
         element-loading-text="拼命加载中">
        <comment-query @query="getList"></comment-query>
        <comment-operate ref="commentOperate" :loading="loading" :filterData="filterData" @query="getList"></comment-operate>
    </div>
</template>

<style scrop lang="less">

    .comment-operate-box {
        width: 100%;
    }

    .comment-box {
        font-size: 16px;
    }

</style>

<script>
import dateFormat from '../../module/DateFormat';
import QuRequest from '../../core/QuRequest';
import sensitiveConfig from '../../module/SensitiveConfig';
import CommentQuery from './CommentQuery';
import CommentOperate from './CommentOperate';

const quRequest = new QuRequest();


export default {
	components: {
		CommentQuery,
		CommentOperate
	},
	data(){
		return {
			filterData : {},
            loading: false
		}
	},
    methods: {
		getList(filter){
			if(!filter){
				filter = this.filterData;
            } else {
				this.filterData = filter;
            }
            this.loading = true;

			this.$updateRouter(filter);
			quRequest.send(quRequest.Apis.qukan.commentAudit.search, filter).then( res => {
				if(res.data && res.data.list){
					this.$refs.commentOperate.setList(res.data);
                } else {
					this.$refs.commentOperate.setList();
                }
            }).catch(err => {
                this.$message({
                    type: 'error',
                    message: err.message
                })
            }).fin( () => {
				this.loading = false;
            })
        }
    },
    mounted() {
//        this.getList();
    }
}
</script>



