<template>
    <div class="tile is-ancestor">
        <div class="tile is-parent is-4">
            <article class="tile is-child box comment-operate-box">
                <div class="comment-operate">
                    <div class="operate-header">
                        <el-row type="flex" class="row-bg" :gutter='5' justify="space-between">
                            <el-col :lg="6">
                                <el-button type="danger"
                                           class="col-item"
                                           @click="deleteAll()">
                                    全删
                                </el-button>
                            </el-col>


                            <el-col :lg="6" v-show="canPage">
                                <el-button type="default"
                                           class="col-item"
                                           @click="handlePage(1)">
                                    上一页
                                </el-button>
                            </el-col>

                            <el-col :lg="6" v-show="canPage">
                                <el-button type="default"
                                           class="col-item"
                                           @click="handlePage(2)">
                                    下一页
                                </el-button>
                            </el-col>

                            <el-col :lg="6">
                                <el-button type="primary"
                                           class="col-item "
                                           @click="operateSubmit()">
                                    提交
                                </el-button>
                            </el-col>
                        </el-row>
                        <el-row>
                            <el-col>
                                <el-radio-group v-model="radioOperate" class="radio-group">
                                    <el-radio-button :label="item.key"
                                                     :key="item.key"
                                                     v-for="item in status.radioButton"
                                                     class="radio-group-item">
                                        {{item.name}}
                                    </el-radio-button>
                                </el-radio-group>
                            </el-col>
                        </el-row>
                    </div>
                    <div class="operate-list"
                         @click="arrowSelect">
                        <div class="list-item"
                             v-for="(item, index) in list"
                             :key="item.comment_id + '_' + item.content_id"
                             :class="[
                             	item.attr.selected ? 'selected' : '',
                             	item.attr.isHot ? 'hot' : '',
                             	item.attr.isQuality ? 'quality' : '',
                             	item.attr.isPost ? 'bold' : '',
                                item.attr.operate]"
                             :title="item.content_info.title"
                             @click="selectContent(index, item)">
                            {{item.content_info.title}}
                        </div>
                    </div>
                </div>
            </article>
        </div>
        <div class="tile is-parent is-8">
            <article class="tile is-child box">
                <div class="comment-detail-header">
                    <div class="header">
                        <div class="setting-info">
                            <div class="all-count">
                                <span v-if="canPage" class="tip-item">第 {{filterData.page}} 页, 共 {{ totalPage }} 页</span>
                                <span class="tip-item">查询返回<span>{{list.length}}</span>条</span>
                                <span class="tip-item">查询结果总计{{allCount}}条</span>
                            </div>
                            <div class="setting">
                                <el-button type="info"
                                           size="small"
                                           class="col-item"
                                           @click="setExtra('isHot')">
                                    {{ !extraHot ? '设为热门' : '取消热门' }}
                                </el-button>
                                <el-button type="info"
                                           size="small"
                                           class="col-item"
                                           @click="setExtra('isQuality')">
                                    {{ !extraQuality ? '设为优质' : '取消优质'}}
                                </el-button>
                            </div>
                        </div>
                    </div>
                    <comment-detail v-show="selectIndex > -1" ref="commentDetail"></comment-detail>
                </div>
            </article>
        </div>
    </div>
</template>

<style lang="less">
    .setting-info {
        height: 38px;
        line-height: 38px;
        margin-bottom: 10px;
        .all-count {
            float: left;
        }
        .setting {
            float: right;
        }
    }

    .tip-item {
        margin-right: 20px;
    }

    .comment-operate {
        width: 100%;
        .col-item {
            width: 100%;
        }

        .radio-group {
            width: 100%;

            .radio-group-item {
                width: 25%;

                .el-radio-button__inner {
                    width: 100%;
                }
            }
        }



        .pos-right {
            float: right;
        }

        .operate-header {
            margin-bottom: 10px;
        }

        @color: rgb(191, 217, 216);

        .left-pos {
            position: relative;
            top: -5px;
            left: 0px;
            font-size: 12px;
            /*color: red;*/
        }

        .operate-list {
            width: 100%;
            padding: 10px 0px;
            border-top: 1px solid @color;

            .list-item {
                white-space: nowrap;
                overflow: hidden;
                text-overflow: ellipsis;
                height: 24px;
                line-height: 24px;
                margin: 6px 0;
                position: relative;
                cursor: pointer;

                &.selected {
                    background: rgb(225, 225, 225);
                }

                &.bold {
                    font-weight: bold;
                }

                &.wait {
                    color: black;
                }

                &.pass {
                    color: green;
                }

                &.self {
                    color: orange;
                }

                &.deny {
                    color: red;
                }

                &.cold {
                    text-decoration: underline;
                }

                &.hot:before {
                    content: '[热门]';
                    .left-pos;
                }

                &.quality:before {
                    content: '[优质]';
                    .left-pos;
                }

                &.hot.quality:before {
                    content: '[热门 优质]';
                    .left-pos
                }

            }
        }
    }

</style>

<script>
    import dateFormat from '../../module/DateFormat';
    import QuRequest from '../../core/QuRequest';
    import CommentDetail from './CommentDetail';
    import $ from 'jquery';
    import {
        COMMENT,
        commentStatus
    } from '../../module/CommentConfig';

    const quRequest = new QuRequest();

    const SHORT_KEY = {
        DELETE: 'D',
        ALL_DELETE: 'A',
        HIDDEN: 'H',
        PASS: 'S',
        SUBMIT: 'G',
        HOT: 'T',
        QUALITY: 'Q',
        OFFLINE: 'F',
        SEARCH: 'C',
        DOWN: 'x',
        UP: 'w',
        PAGE_UP: 'N',
        PAGE_DOWN: 'M'
    };

    const radioButton = {
        [COMMENT.PASS]: {
            key: 'pass',
            name: '通过',
            short: SHORT_KEY.PASS
        },
        [COMMENT.SELF]: {
            key: 'self',
            name: '隐藏',
            short: SHORT_KEY.HIDDEN
        },
        [COMMENT.DENY]: {
            key: 'deny',
            name: '删除',
            short: SHORT_KEY.DELETE
        },
        [COMMENT.COLD]: {
            key: 'cold',
            name: '下热门',
            short: SHORT_KEY.OFFLINE
        }
    };

    export default {
        components: {
            CommentDetail
        },
        props: [ 'filterData', 'loading' ],
        data(){
            return {
                status: {
                    radioButton: radioButton
                },
                list: [],
                allCount: 0,
                totalPage: 1,
                selectIndex: -1,
                queryData: {}
            }
        },
        computed: {
            radioOperate: {
                get(){
                    return this.getAttribute('operate');
                },
                set(newValue){
                    let attr = this.getAttribute('operate');
                    if (attr !== newValue) {
                        this.setAttribute('isPost', false);
                    }
                    this.setAttribute('operate', newValue);
                    this.setNextSelect();
                }
            },
            extraHot(){
                return this.getAttribute('isHot');
            },
            extraQuality(){
                return this.getAttribute('isQuality');
            },
            canPage() {
                let statusKey = this.filterData.cmt_status;
                return !commentStatus.every(value => {
                    return !(value.key === statusKey && value.canPage);
                });
            }
        },
        methods: {
            handlePage(direction) {
                if (!this.canPage || this.loading) { return; }
                let isChanged = false;
                let { filterData, totalPage } = this;
                let isUp = direction === 1;
                if (isUp && filterData.page > 1) {
                    filterData.page--;
                    isChanged = true;
                } else if ( !isUp && filterData.page < totalPage ) {
                    filterData.page++;
                    isChanged = true;
                }
                if (isChanged) {
                    this.$emit('query', filterData);
                }
            },
            setList(data){
                let list = data.list || [];
                this.allCount = data.total || 0;
                this.totalPage = data.total_page;
                /* 测试数据
                list = [
                    {
                        content_info: {
                            title: 'haha, 新内容测试'
                        },
                        member_info:{}
                    },
                    {
                        content_info: {
                            title: 'haha, 新内容测试'
                        },
                        member_info:{}
                    },
                    {
                        content_info: {
                            title: 'haha, 新内容测试'
                        },
                        member_info:{}
                    },
                    {
                        content_info: {
                            title: 'haha, 新内容测试'
                        },
                        member_info:{}
                    },
                    {
                        content_info: {
                            title: 'haha, 新内容测试'
                        },
                        member_info:{}
                    }
                ]*/
                let array = [];
                list.forEach(item => {
                    if (item && item.content_info) {
                        item.attr = {
                            'selected': false,
                            'operate': '',
                            'isHot': item.is_top,
                            'isQuality': item.is_good,
                            'checkPhone': item.member_is_grey,
                            'checkIp': item.ip_is_grey,
                            'checkTitle': item.content_is_grey,
                            'isPost': false
                        };
                        if (item.status && radioButton[item.status]) {
                            item.attr.operate = radioButton[item.status].key;
                            item.attr.isPost = true;
                        }
                        let queryId = item.content_id + ':' + item.comment_id;
                        item.queryId = queryId;
                        this.queryData[queryId] = item;
                        array.push(item);
                    }
                });
                this.list = array;
                if (array && array.length > 0) {
                    this.selectContent(0);
                } else {
                    this.selectContent(-1);
                }
            },
            selectContent(index){
                this.setAttribute('selected', false);
                this.selectIndex = index;
                if (index > -1) {
                    this.setAttribute('selected', true);
                    this.$refs.commentDetail.setDetail(this.list[index]);
                } else {
                    this.$refs.commentDetail.setDetail();
                }
            },
            setNextSelect(){
                let nextIndex = -1;
		        for(let i = this.selectIndex + 1, len = this.list.length; i < len; i++){
		            let item = this.list[i];
		            if(item.attr ){ // && !item.attr.operate
		                nextIndex = i;
		                this.selectContent(nextIndex);
		                break;
                    }
                }
                return nextIndex;
            },
            setAttribute(type, value){
                if (this.selectIndex > -1 && this.list[this.selectIndex]) {
                    let attr = this.list[this.selectIndex].attr;
                    if (typeof value !== 'undefined') {
                        attr[type] = value;
                    } else {
                        attr[type] = !attr[type];
                    }
                    return attr[type];
                }
            },
            getAttribute(type){
                if (this.selectIndex > -1 && this.list[this.selectIndex]) {
                    let attr = this.list[this.selectIndex].attr;
                    return attr[type];
                }
                return false;
            },
            setExtra(type){
                this.setAttribute('isPost', false);
                this.setAttribute(type);
            },
            arrowSelect(event){
                let keyType = event.keyType;
                if (keyType) {
                    if (keyType === SHORT_KEY.UP.toLocaleLowerCase()) {
                        if (this.selectIndex > 0) {
                            this.selectContent(this.selectIndex - 1)
                        }
                    } else {
                        if (this.selectIndex < this.list.length - 1) {
                            this.selectContent(this.selectIndex + 1)
                        }
                    }
                }
            },
            operateSubmit(){
                let operateList = this.getAudiList();
                if (operateList && operateList.length > 0) {
                    this.$confirm('是否确认将现有标记状态提交？', '提示', {
                        confirmButtonText: '确定',
                        cancelButtonText: '取消',
                        type: 'warning'
                    }).then(() => {
                        this.audit(operateList);
                    }).catch(() => {

                    });
                } else {
                    this.$alert('没有内容被标记', '提示', {
                        confirmButtonText: '确定',
                        callback: action => {

                        }
                    });
                }
            },
            deleteAll(){
                this.list.forEach(item => {
                    if (item.attr) {
                        item.attr.operate = 'deny';
                    }
                })
            },
            getAudiList(){
                let array = [];
                this.list.forEach(item => {
                    if (!item.attr.isPost && item.attr.operate) {
                        let action = {
                            cid: item.content_id,
                            cmt_id: item.comment_id,
                            act: item.attr.operate,
                            extra: []
                        };

                        if (item.attr.isHot) {
                            action.extra.push('top')
                        } else {
                            action.extra.push('untop')
                        }

                        if (item.attr.isQuality) {
                            action.extra.push('good')
                        } else {
                            action.extra.push('ungood')
                        }
                        array.push(action);
                    }
                });
                return array;
            },
            audit(array){
                quRequest.send(quRequest.Apis.qukan.commentAudit.audit, {
                    v: JSON.stringify(array)
                }).then(res => {
                    array.forEach(item => {
                        let queryId = item.cid + ':' + item.cmt_id;
                        if (this.queryData[queryId]) {
                            this.queryData[queryId].attr.isPost = true;
                        }
                    });
                    this.checkAudit();
                })
        },
            checkAudit(){
                let isQuery = true;
                let that = this;
                this.list.forEach( item => {
                    if(isQuery && item.attr && !item.attr.isPost){
                        isQuery = false;
                    }
                })
                if(isQuery){
                    this.$message({
                        message: "提交成功，更新下一页数据！",
                        type: 'success',
                        duration: 1000,
                        onClose(){
                            that.queryData = {};
                            that.$emit('query');
                        }
                    });
                }
            }
        },
        mounted() {

        }
    }
</script>



