<template>
    <div class="comment-detail-box" >
        <div class="about-info clearfix">
            <div class="avatar">
                <img :src="member.avatar" class="user-avatar" width="58" height="58" alt="">
            </div>
            <div class="con-info">
                <div>
                    <div class="tip-item">
                        频道：{{content.type_name}}
                    </div>
                    <div class="tip-item">
                        内容ID：{{content.id}}
                    </div>
                    <div class="tip-item" v-if="content.source_category">
                        {{content.source_category.source_category_name}}：{{content.source_category.name}}
                    </div>
                </div>
                <div>
                    <div class="tip-item">
                        发评人：{{member.nickname}}({{member.member_id}})
                    </div>
                    <div class="tip-item">
                        手机：{{member.telephone}}
                        <el-button type="text"
                                   @click="putCheck('1', member.member_id, 'checkPhone')">
                            [{{ !attribute.checkPhone ? '加入' : '取消'}}必审]
                        </el-button>
                    </div>
                    <div class="tip-item">
                        发评IP:{{detail.ip}}
                        <el-button type="text"
                                   @click="putCheck('3', detail.ip, 'checkIp')">
                            [{{ !attribute.checkIp ? '加入' : '取消'}}必审]
                        </el-button>
                    </div>
                </div>
            </div>
        </div>
        <div class="content-info">
            <div class="sensitive-word">
                敏感词：<span v-html="formatSensitive"></span>
            </div>
            <div class="content-title">
                标题：
                <a :href="content.url" target="_blank">{{content.title}}</a>
                <el-button type="text"
                           @click="putCheck('2', detail.content_id, 'checkTitle')">
                    [{{ !attribute.checkTitle ? '加入' : '取消'}}必审]
                </el-button>
            </div>
            <div class="content-list">
                <el-card class="box-card content-item" v-show="!hasMain">
                    <div class="relay-title">
                        <div class="replay-user">
                            {{member.nickname}}：
                        </div>
                        <div class="replay-time">
                            {{detail.cmt_create_time}}
                        </div>
                    </div>
                    <div class="replay-content"
                         v-html="formatComment">
                    </div>
                </el-card>
                <el-card class="box-card content-item" v-show="hasMain">
                    <div class="main-title">
                        <div class="relay-title">
                            <div class="replay-user">
                                {{mainComment.nickname}}：
                            </div>
                            <div class="replay-time">
                                {{mainComment.create_time}}
                            </div>
                        </div>
                        <div class="replay-content">
                            {{mainComment.comment}}
                            <el-card class="box-card content-item" >
                                <div class="relay-title">
                                    <div class="replay-user">
                                        {{formatReply}}：
                                    </div>
                                    <div class="replay-time">
                                        {{detail.cmt_create_time}}
                                    </div>
                                </div>
                                <div class="replay-content"
                                     v-html="formatComment"></div>
                            </el-card>
                        </div>
                    </div>
                </el-card>
            </div>
        </div>
    </div>
</template>

<style lang="less" scoped >
    .comment-detail-box {
        .col-item {
            margin-bottom: 10px;
        }

        .about-info {
            .avatar{
                float: left;
                margin-right:15px;
                height:58px;
                background:#ccc;
            }
            .con-info{
                float: left;
            }
            .tip-item {
                display: inline-block;
                margin-right: 20px;
            }
            .col-item;
        }

        .setting-info {
            height: 38px;
            line-height: 38px;
            .col-item;
            .all-count {
                float: left;
            }
            .setting {
                float: right;
            }
        }

        .content-info {
            border-top: 1px solid rgb(191, 217, 216);
            padding-top: 10px;
            .content-list {
                .content-item {
                    margin: 15px 0;
                    .replay-user {
                        display: inline-block;
                    }
                    .replay-time {
                        float: right;
                    }
                }
            }
        }
    }
</style>

<script>
import dateFormat from '../../module/DateFormat';
import QuRequest from '../../core/QuRequest';
import sensitivityCheck from '../../module/SensitivityCheck';

const quRequest = new QuRequest();

export default {
    data(){
        return {
            detail: {},
            content: {
				source_category: {}
            },
            attribute: {},
            member: {},
            mainComment: [],
            hasMain: false
        }
    },
	computed: {
		formatSensitive(){
			let list = this.detail.sensitive_words || [];
			let array = [];
			list.forEach(item => {
				array.push(sensitivityCheck.highSub(item))
			});
			return array.join(',');
		},
		formatComment(){
			let comment = this.detail.comment;
			let sensitive = this.detail.sensitive_words;
			if(comment && sensitive){
				let words = sensitivityCheck.highWord(comment, sensitive, true);
				return words;
			} else {
				return ''
			}
		},
		formatReply(){
			let reply = this.member.nickname;
			if(this.detail.ref_member_id !== '0'){
				reply += '回复' + this.detail.replied_member_nickname
			}
			return reply;
		}
    },
    methods: {
		setDetail(detail){
			if(detail){
				this.detail = detail;
				this.content = this.detail.content_info;
				this.attribute = this.detail.attr;
				this.member = this.detail.member_info;
				console.log(this.member);
				this.hasMain = false;
				if(this.detail.main_comment && this.detail.main_comment.comment){
					this.hasMain = true;
					this.mainComment = this.detail.main_comment;
				}
            } else {
				this.detail = {};
				this.content = {
					source_category: {}
				};
				this.attribute = {};
				this.member = {};
				this.mainComment = [];
                this.hasMain = false;
            }
		},

		putCheck(type, id, attr){
			quRequest.send(quRequest.Apis.qukan.commentAudit.operateGreyList, {
				target_id: id , // string目标id.用户/文章/ip
                target_type: type, //number目标类型.用户 1/文章 2/ip 3
			    action: !this.attribute[attr] ? 'add': 'del'	//string 操作类型.add/del
            }).then( res => {
            	this.attribute[attr] = !this.attribute[attr];
            })
        }
    },
    mounted() {

    }
}
</script>



